    width: 100%;
    box-sizing: border-box;
    display: block;
    font-weight: bold;
    float: right;
}

  
.NavItem_NavItem__1UEFu a:hover,
.NavItem_NavItem__1UEFu a:active,
.NavItem_NavItem__1UEFu a.NavItem_active__rMvU- {
    color: #C50A5B;
}

@media (min-width: 500px) and (max-width: 950px) {
    .NavItem_NavItem__1UEFu {
        margin: 0.2em;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
    }
    
    .NavItem_NavItem__1UEFu a {
        height: 100%;
        padding: 16px 10px;
        font: inherit;
        font-weight: bold;
        background-color: transparent;
        
    }
    
    .NavItem_NavItem__1UEFu a:hover,
    .NavItem_NavItem__1UEFu a:active,
    .NavItem_NavItem__1UEFu a.NavItem_active__rMvU- {
        color: #C50A5B;
        border-bottom: 4px solid #C50A5B;
    }
}
